// components/ThemeSwitcher.tsx
import { useEffect, useState } from 'react';

const ThemeSwitcher = () => {
  // State to track the current theme (true for dark, false for light)
  const [isDark, setIsDark] = useState(false);

  // Use effect to load the user's theme preference from localStorage
  useEffect(() => {
    const savedTheme = localStorage.getItem('theme') || 'light';
    document.documentElement.setAttribute('data-theme', savedTheme);
    setIsDark(savedTheme === 'dark');
  }, []);

  // Toggle the theme between light and dark
  const toggleTheme = () => {
    const newTheme = isDark ? 'light' : 'dark';
    document.documentElement.setAttribute('data-theme', newTheme);
    localStorage.setItem('theme', newTheme); // Save to localStorage for persistence
    setIsDark(!isDark);
  };

  return (
    <div className="flex items-center space-x-2">
      <span className="text-lg">主题切换</span>
      <input
        type="checkbox"
        className="toggle toggle-accent"
        checked={isDark}
        onChange={toggleTheme}
      />
    </div>
  );
};

export default ThemeSwitcher;
